.App {
  padding: 50px;

  display: inline-block;
  color: black;
  text-align: center;
  width: 25%;
  cursor: pointer;
  transition: transform 0.2s;

  margin: 0 auto;
}

.App img {
  border: 2px solid lightblue;
  border-radius: 10px;

  max-width: 100%;

  height: auto;
}
.App:hover {
  -ms-transform: scale(1.3); /* IE 9 */
  -webkit-transform: scale(1.3); /* Safari 3-8 */
  transform: scale(1.2);
}

.MovieList {
  margin: 10px;
  padding: 10px;
}

.Navbar {
  margin: 0px;
  height: 100%;
  text-align: center;
  color: black;
}

.Dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2px;

  color: black;
}

/* On screens that are 996px or less */
@media screen and (max-width: 1125px) {
  .App {
    display: inline-block;

    width: 100%;
  }
}
